.garvishon{
    width: 250px;
    max-width: 250px;
    border: 1px solid;
    padding: 10px;
    border-radius: 5px;
    margin-left:10px;
    margin-right:10px;
   

}
.garvishon_footer{
    padding:10px 0px;
    color: white;
    text-align:center;
    font-size:15;
    font-weight:300
}

.feed_header{
    text-align:right;
    color:sky;
}
.spinner_container{
    transform: translate(-50%,-50%);
    left: 50%;
    top: 50%;
    position: absolute;
}
.feed_container{
    transform: translate(-50%,-50%);
    left: 50%;
    top: 50%;
    position: absolute;
    text-align:left;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

h1{
    font-weight:500
}

.garvishon_content{
    min-height:250px;
}